.carousel {
    height: 30vw;
    // border-top: 1px solid tomato;
    // display: flex;
    width: 100%;
    background-size: cover;
    background-position: center;
    // display: flex;
    // align-items: flex-end;
    
  }
  .cover-logo {
    position: absolute;
    // top: 1em;
    margin: 0 0 0 75vw;
    // margin-left: 70%;
    // right: 5px;
    // background-color: green;
    background-image: url('../../assets/cover-logo.jpg');
    background-size: cover;
    background-position: center;
    height: 65px;
    width: 65px;
   
    // z-index: +3;
  }
  .carousel-tag {
    position: absolute;
    bottom: 1em ;
    margin: 0 0 2em 3em;
    // padding: .5em 0 .5em 1em ;
    border-left: 1px solid tomato;
    color:#fefefe;
    text-shadow: 1px 1px 6px grey;
    background-color: rgba(0, 0, 0, 0.2);
    // background-opacity: 10%;
    // animation-iteration-count: 10 3s;
  }  
  .carousel-tag-h {
    margin: 0 10px;
    font-weight: 200;
    font-family: 'Poiret One', cursive;

  }
  .carousel-tag-h2 {
    margin: 0 10px;
    font-weight: 200;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    font-weight: 450;
  }
  .carousel-tag-p {
    margin: 0 10px;
  }
  .slick-slide img {
    margin: auto;
  }
.video {
  width: 100%;
}

