.cell {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin: 10em 0;
}

.container {
    max-width: 1200px;
}

.vision {
    // height: 320px;
    width: 100%;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    align-content: center;
    justify-content: center;
    text-align: center;
    // border-bottom:1px solid gray;
    .vision-title {
        font-family: 'Poiret One', cursive;
        font-weight: 400;
        font-size: 2.4em;
        padding: 0 0 1em 0;
        color: tomato;
        
    }
    .vision-content {
        // width: 70%;
        font-family: 'Open Sans', sans-serif;
        font-weight: 200;
        font-size: medium;
        letter-spacing: 0.5px;
        span {
            color:rgb(63, 176, 251);
        }
        // line-height: 20px;
    }
}

.section-title {
    width: 100%;
    font-family:'Open Sans', sans-serif;
    font-size: large;
    font-weight: 400;
    margin: 0;
    letter-spacing: 2px;
    margin-bottom: 2em;
    text-align: center;
    background-color:rgba(135, 207, 255, 0.2);
    -webkit-backdrop-filter:saturate(180%) blur(10px);
    backdrop-filter: saturate(180%) blur(10px);

}
.services {
    display:flex;
    flex-wrap: wrap;
    justify-content: space-around;
}


.projects-div {
    
    .projects-home {
        // padding: 50px 0 50px 0;
        width: 90%;
        display: flex;
        justify-content: space-between;
        align-content: center;
    }
}
.flagholder {
    // position: absolute;
    // background-color: skyblue;
    box-shadow: 4px 4px 10px grey;
    overflow: hidden;
    .background-image{
        width: 200px;
        height: 300px;
    }
    
    &:hover {
        cursor: pointer;
        & .background-image {
          transform: scale(1.1);
          transition: transform 1s cubic-bezier(0.25, 0.45, 0.45, 0.95);
        }
    }
}

@media screen and (max-width:800px) {
    .vision {
        .vision-title{
            font-size: x-large;
        }
    }
    .cell {
        margin: 5em 0;
    }
}